footer {
    position: absolute;
    left: 0;
    right: 0;
    .footer-inner {
        background-color: #7882A4;
        .container {
            display: flex;
            justify-content: flex-end;
            height: 80px;
            padding: 20px;
            font-size: 14px;
            color: #fff;
        }
    }

    // Wave
    .custom-shape-divider-bottom-1656519457 {
        position: absolute;
        bottom: 78px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        transform: rotate(180deg);
    }
    .custom-shape-divider-bottom-1656519457 svg {
        position: relative;
        display: block;
        width: calc(114% + 1.3px);
        height: 60px;
    }
    .custom-shape-divider-bottom-1656519457 .shape-fill {
        fill: #7882A4;
    }
}